.widget {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search post type

    &.mkdf-search-post-type-widget {
        .mkdf-post-type-search-field {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 3px;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            float: left;
            font-family: $default-text-font;
            font-size: 12px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            outline: 0;
            padding: 0 25px 0 20px;
            position: relative;
            vertical-align: top;
            width: 100%; /* width of loading icon */

            @include mkdfPlaceholder {
                color: $widget-text-color;
            }

            @include mkdfTransition(color .2s ease-in-out);

            -webkit-appearance: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent posts

    &.mkdf-recent-post-widget {

        .mkdf-widget-title-holder {
            margin-bottom: 31px;
        }

        .mkdf-recent-posts {
            .mkdf-rp-item {
                margin: 0 0 10px;
                padding: 0;
                text-align: left;

                a {
                    display: table;
                    width: 100%;

                    .mkdf-rp-image,
                    .mkdf-rp-title {
                        display: table-cell;
                        vertical-align: top;
                    }

                    .mkdf-rp-title {
                        @include mkdfTransition(color .2s ease-in-out);
                        font-weight: 500;
                        color: $default-heading-color;
                    }

                    .mkdf-rp-image {
                        height: 87px;
                        padding-right: 24px;
                        width: 87px;

                        img {
                            display: block;
                        }
                    }

                    &:hover {
                        .mkdf-rp-title {
                            color: $gray-color;
                        }
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////   CF7

    &.mkdf-contact-form-7-widget {
        .mkdf-widget-title-holder {
            margin-bottom: 15px;
        }

        .ajax-loader {
            display: none;
        }
    }
}