@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 52px 0 48px;
    }

    .mkdf-comment {

        &.mkdf-pingback-comment {

            .mkdf-comment-text {
                padding: 0;
            }
        }
    }

    .mkdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        .mkdf-comment {
            border: 1px solid $default-border-color;
            padding: 22px 29px 27px;
            box-sizing: border-box;
        }

        li {
            @include mkdfRelativeHolderLayout();
            margin: 40px 0 0;
            padding: 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 145px;

            li {
                position: relative;

                &:before {
                    content: "\4a";
                    font-family: "ElegantIcons";
                    font-size: 30px;
                    position: absolute;
                    top: -8px;
                    left: -73px;
                    color: #f2756a;
                    @include mkdfTransform(rotate(180deg));
                }
            }
        }
    }

    .mkdf-comments-title {
        margin-bottom: 26px;

        > * {
            margin: 0;
        }

        > h3 {
            font-size: 19px;
        }
    }

    .mkdf-comment-image {
        position: relative;
        top: 10px;
        left: 0;
        width: 100px;
        height: 100px;
        display: block;
        float: left;

        img {
            display: block;
        }
    }

    .mkdf-comment-text {
        padding: 0 0 0 124px;
        min-height: 100px;

        .mkdf-comment-info {
            margin: 6px 0 7px;
            display: inline-block;
            width: auto;
            float: left;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: left;
            margin-right: 10px;
            font-size: 12px;
            font-weight: 500;
            color: $default-heading-color;
            text-transform: uppercase;
            letter-spacing: .2em;
            font-family: $default-heading-font;

            &:hover {
                color: $gray-color;
            }
        }

        .comment-edit-link {
            color: $first-main-color;
        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
            font-weight: 500;
        }

        .mkdf-comment-date {
            display: inline-block;
            margin: 0;
            font-size:  15px;
            font-style: italic;
            color: #f2756a;
            line-height: 2.1em;
            padding-left: 11px;
        }

        .mkdf-text-holder p {
            margin: 7px 0 22px;
            color: #838383;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        .comment-respond + .comment-reply-link {
            display: none;
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .mkdf-comment-form-inner {
        padding: 25px 0 78px;
    }

    .comment-reply-title {
        margin: 0 0 23px;
    }

    h3.comment-reply-title {
        font-size: 19px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {

            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
    @include mkdfButtonDefaultStyle();
    @include mkdfButtonSize();
    @include mkdfButtonSolidColor();
    cursor: pointer;

    &:hover {
        @include mkdfButtonSolidHoverColor();
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond {
    input[type='text'] {
        width: calc(33.33% - 11.5px);
        float: left;
        margin-right: 17px;
        margin-bottom: 15px;
    }

    #url {
        margin-right: 0;
    }

    .comment-form-cookies-consent {
        font-size: 12px;
        margin-bottom: 19px;

        #wp-comment-cookies-consent {
            position: relative;
            top: 1px;
        }
    }
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
    margin-bottom: 17px;
    max-width: 100%;
}

.mkdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.mkdf-form-travel {
    .wpcf7-form-control.wpcf7-text,
    .wpcf7-form-control.wpcf7-select,
    input.wpcf7-form-control.wpcf7-submit{
        width: calc(25% - 8px);
        margin: 0 10px 0 0;
        vertical-align: middle;

        @include ipad-landscape {
            width: 47%;
            margin: 5px 10px 5px 0;
        }
        @include ipad-portrait {
            width: 47%;
            margin: 5px 10px 5px 0;
        }
    }

    .wpcf7-form-control.wpcf7-text {
        padding: 11px 16px 12px;
    }

    .wpcf7-form-control.wpcf7-select{
        padding: 0 16px 0;
        box-sizing: border-box;
        cursor: pointer;
        height: 58px;
    }

    input.wpcf7-form-control.wpcf7-submit {
        padding: 0 16px 0;
        margin: 0;
        height: 56px;
    }

    .ajax-loader {
        display: none;
    }

    @include phone-landscape {
        .wpcf7-form-control.wpcf7-text,
        .wpcf7-form-control.wpcf7-select,
        input.wpcf7-form-control.wpcf7-submit{
            width: 100%;
            margin: 0 0 10px;
        }
    }
}

.mkdf-form-newsletter {
    margin: auto;

    @include ipad-landscape {
        width: 95%;
    }

    @include ipad-portrait {
        width: 75%;
    }

    .wpcf7-form-control.wpcf7-text {
        text-align: center;
        margin-bottom: 13px;
        font-style: italic;
    }

    input.wpcf7-form-control.wpcf7-submit {
        width: 100%;
    }

    .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.mkdf-slider-is-moving {
        .owl-item {
            .mkdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.mkdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include mkdfTransform(translateY($pos));
            }
        }
    }

    &.mkdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            @include mkdfTransform(translateY(-50%));
            @include mkdfTransition(color .2s ease-out);

            &:hover {
                color: $hover-text-color;
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                line-height: 1.1em;
                background-color: $default-background-color;
                padding: 15px 10px;

                svg {
                    height: 15px!important;
                    width: 42px!important;
                }

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: -40px;
        }

        .owl-next {
            right: -40px;
        }

        @include laptop-landscape-mac {
            .owl-prev {
                left: -30px;
            }

            .owl-next {
                right: -30px;
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 6px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 12px;
                border-radius: 3em;
                background-color: transparent;
                border: 2px solid rgba($default-heading-color, .5);
                box-sizing: border-box;

                @include mkdfTransition(background-color .2s ease-out, border .2s ease-out);
            }

            &:hover,
            &.active {

                span {
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

.mkdf-prev-icon {
    svg {
        line {
            @include mkdfTransformOrigin(64% 0);
            @include mkdfTransition(.3s);
        }

        polyline {
            @include mkdfTransition(.3s);
        }
    }

    &:hover {
        svg {
            line {
                @include mkdfTransform(scaleX(.7));
            }
    
            polyline {
                @include mkdfTransform(translateX(5px));
            }
        }
    }
}

.mkdf-next-icon {
    svg {
        line {
            @include mkdfTransformOrigin(64% 0);
            @include mkdfTransition(.3s);
        }

        polyline {
            @include mkdfTransition(.3s);
        }
    }

    &:hover {
        svg {
            line {
                @include mkdfTransform(scaleX(.7));
            }
    
            polyline {
                @include mkdfTransform(translateX(-5px));
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .mkdf-wrapper {
        padding: 0 !important;
    }

    .mkdf-content {
        height: 100vh;
    }

    &.admin-bar {
        .mkdf-content {
            height: calc(100vh - 32px);
        }
    }

    .mkdf-content-inner {
        @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
        @include mkdfTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .mkdf-404-title {
            margin: 0;
        }

        .mkdf-404-subtitle {
            margin: 35px 0 0;
        }

        .mkdf-404-text {
            margin: 10px 0 0;
        }

        .mkdf-btn {
            margin: 30px 0 0;

            &.mkdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .mkdf-content {
            height: 100%;
            min-height: 100vh;
        }

        &.admin-bar {
            .mkdf-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {
        .mkdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 63px;
    right: 37px;
    bottom: 35px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include mkdfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 61px;
        height: 61px;
        line-height: 63px;
        border-radius: 100%;
        font-size: 43px;
        text-align: center;
        font-family: $additional-font;
        color: $default-heading-color;
        background-color: #fff;
        border: 1px solid #fff;
        overflow: hidden;

        @include mkdfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        &:hover {
            background-color: #000;
            border: 1px solid #000;
            color: #fff;
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.mkdf-self-hosted-video-holder {
    .mkdf-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

