aside.mkdf-sidebar,
.elementor-widget-sidebar,
.wpb_widgetised_column {
    // widgets spacing
    $widget-bottom-margin: 37px;
    $widget-bottom-padding: 15px;
    $widget-inner-padding: 15px;

    // widget color
    $widget-background-color: #eeeeee;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 9px;

    // list items color
    $widget-list-item-background-color: #ffffff;

    // list items border
    $widget-list-item-border-color: $default-border-color;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: inherit;
    $widget-heading-color: inherit;

    // links
    $widget-link-color: $gray-color;
    $widget-link-hover-color: $default-heading-color;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';
}

aside.mkdf-sidebar,
.wpb_widgetised_column,
.elementor-widget-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }

        @include ipad-landscape {
            text-align: center;
        }

        &.widget_media_image {
            .mkdf-widget-title-holder {
                margin-bottom: 17px;
            }
        }
    }

    .mkdf-widget-title-holder {
        text-align: center;
        position: relative;
        overflow: hidden;

        .mkdf-widget-title-before {
            content: '';
            height: 1px;
            width: 100%;
            background-color: $default-border-color;
            top: 15px;
            position: absolute;
            left: 0;
        }

        .mkdf-widget-title {
            display: inline-block;
            width: auto;
            margin: 0 0 3px;
            background-color: #ffffff;
            padding: 0 12px;
            z-index: 90;
            position: relative;
            font-weight: 600;
            letter-spacing: .3em;
        }
    }

    div[id*="monster"] {
        .mkdf-widget-title {
            font-size: 12px;
            color: #000!important;
        }
    }
}