footer {
    // widgets spacing
    $widget-bottom-margin: 43px;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 15px;

    // list items color
    $widget-list-item-background-color: #fff;

    // list items border
    $widget-list-item-border-color: #cccccc;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: inherit;
    $widget-heading-color: #999999;

    // links
    $widget-link-color: $gray-color;
    $widget-link-hover-color: $hover-text-color;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';

    .mkdf-footer-top-holder {
        .mkdf-widget-title-holder {
            position: relative;
            text-align: center;
            overflow: hidden;

            .mkdf-widget-title-before {
                content: '';
                height: 1px;
                width: 100%;
                background-color: #f2f2f2;
                top: 15px;
                position: absolute;
                left: 0;
            }

            .mkdf-widget-title {
                display: inline-block;
                width: auto;
                margin: 0 0 3px;
                background-color: #ffffff;
                padding: 0 12px;
                z-index: 90;
                position: relative;
            }
        }

        .mkdf-icon-widget-holder {
            width: 25%;
            float: unset;
        }

        .mkdf-form-newsletter {
            padding: 0 12%;
            box-sizing: border-box;
        }
    }

    .mkdf-footer-bottom-holder {

        .widget {

            &:only-child {
                margin-bottom: 0;
            }

            &.widget_nav_menu {

                text-align: right;
                float: right;

                ul {
                    li {
                        float: left;
                        padding: 0;
                        line-height: 27px;

                        &:last-child {
                            padding-bottom: 0;
                        }

                        a {
                            &:hover {
                                color: $default-heading-color;
                            }
                        }
                    }
                }
            }

            &.widget_text {

                p {
                    &:first-child {
                        padding-top: 0;
                    }
                }
            }
        }
    }

    .widget {
        &.widget_media_image {
            .mkdf-widget-title-holder {
                margin-bottom: 40px;
            }
        }
    }

    div[id*="monster"] {
        .mkdf-widget-title {
            font-size: 12px;
            color: #000!important;
        }
    }
}

@include phone-landscape {
    footer .mkdf-footer-top-holder .mkdf-form-newsletter {
        padding: 0 1%;
    }
}