.mkdf-page-header,
.mkdf-top-bar,
.mkdf-sticky-header,
.mkdf-mobile-header {

    div [id*="monster"] {
        display: none;
    }

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        position: relative;

        // prevent monster widget from displaying in our header widget areas
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_search {
            display: none !important;
        }

        .mkdf-custom-text-widget {

            > ul {
                margin: 0;

               > li {
                   list-style-type:  none;
                   font-family: $default-heading-font;
                   font-size: 10px;
                   font-weight: 500;
                   letter-spacing: .33em;
                   text-transform: uppercase;
                   text-align: left;

                   .mkdf-header-centered-logo-down & {
                       padding: 15px 0;
                       box-sizing: border-box;
                   }

                   .mkdf-header-divided & {
                       padding: 32px 0;
                       box-sizing: border-box;
                   }

                    .mkdf-custom-text-toggle {
                        img {
                            display: inline;
                            position: relative;
                            top: 5px;
                            margin-right: 10px;
                        }
                    }

                   &:hover {
                       .sub-menu {
                           opacity: 1;
                           visibility: visible;
                           @include mkdfTransition(all .25s ease);
                       }
                   }

                    .sub-menu {
                        position: absolute;
                        top: calc(100% - 1px);
                        left: 0;
                        width: auto;
                        min-width: 142px;
                        padding: 10px 30px;
                        margin: 0;
                        list-style: none;
                        z-index: 10;
                        opacity: 0;
                        visibility: hidden;
                        box-sizing: border-box;
                        background-color: #fff;
                        @include mkdfTransition(all .25s ease);
                        border: 1px solid #f3f3f3;
                        text-align: left;

                        li {
                            .mkdf-custom-text-item {
                                color: $default-text-color !important;
                                font-family: $default-text-font;
                                font-size: 15px;
                                font-weight: 400;
                                text-transform: capitalize;
                                letter-spacing: .02em;
                                @include mkdfTransition(all .2s ease);

                                span {
                                    .mkdf-light-header &{
                                        color: $gray-color !important;

                                        &:hover {
                                            color: $default-text-color !important;
                                        }
                                    }

                                    .mkdf-dark-header & {
                                        color: $default-text-color !important;

                                        &:hover {
                                            color: $gray-color !important;
                                        }
                                    }
                                }

                                &:before {
                                    content: '\25CF';
                                    color: #f7775e;
                                    opacity: 0;
                                    -webkit-transition: all .2s ease;
                                    -moz-transition: all .2s ease;
                                    transition: all .2s ease;
                                    position: absolute;
                                    left: 23px;
                                }

                                &:hover {
                                    color: $gray-color !important;
                                    padding-left: 13px;

                                    &:before {
                                        opacity: 1;
                                        -webkit-transition: all .2s ease;
                                        -moz-transition: all .2s ease;
                                        transition: all .2s ease;
                                        left: 28px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}