.mkdf-top-bar {
    .widget {
        color: #ffffff;

        a {
            color: #ffffff;

            &:hover {
                color:rgba(226, 226, 226, 0.9);
            }
        }

        &.widget_custom_html {
            height: 100%;

            .mkdf-custom-text-widget {
                > ul {
                    margin: 0;

                    > li {
                        list-style-type: none;
                        font-family: $default-heading-font;
                        font-size: 10px;
                        font-weight: 500;
                        letter-spacing: .33em;
                        text-transform: uppercase;
                        text-align: left;
                        line-height: 2.7em;

                        .mkdf-header-centered-logo-down & {
                            padding: 7px 0;
                            box-sizing: border-box;
                        }

                        .mkdf-header-divided & {
                            padding: 32px 0;
                            box-sizing: border-box;
                        }

                        .mkdf-custom-text-toggle {
                            img {
                                display: inline;
                                position: relative;
                                top: 5px;
                                margin-right: 10px;
                            }
                        }

                        &:hover {
                            .sub-menu {
                                opacity: 1;
                                visibility: visible;
                                @include mkdfTransition(all .25s ease);
                            }
                        }

                        .sub-menu {
                            position: absolute;
                            top: 100%;
                            left: 0;
                            width: auto;
                            padding: 10px 30px;
                            margin: 0;
                            list-style: none;
                            z-index: 10;
                            opacity: 0;
                            visibility: hidden;
                            box-sizing: border-box;
                            background-color: #fff;
                            @include mkdfTransition(all .25s ease);
                            border: 1px solid #f3f3f3;
                            text-align: left;

                            li {
                                .mkdf-custom-text-item {
                                    color: $default-text-color !important;
                                    font-family: $default-text-font;
                                    font-size: 15px;
                                    font-weight: 400;
                                    text-transform: capitalize;
                                    letter-spacing: .02em;

                                    span {
                                        .mkdf-light-header & {
                                            color: $gray-color !important;

                                            &:hover {
                                                color: $default-text-color !important;
                                            }
                                        }

                                        .mkdf-dark-header & {
                                            color: $default-text-color !important;

                                            &:hover {
                                                color: $gray-color !important;
                                            }
                                        }
                                    }

                                    &:hover {
                                        color: $gray-color !important;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}